import React from 'react';
import BaseErrorPage from '../../components/errors/BaseErrorPage';
import { DocumentMagnifyingGlassIcon } from '../../components/errors/Icons';

const NotFoundPage = () => {
  console.log(' NotFoundPage (404) rendered');

  return (
    <BaseErrorPage
      icon={DocumentMagnifyingGlassIcon}
      statusCode="404"
      title="Halaman Tidak Ditemukan"
      message="Maaf, halaman yang Anda cari tidak dapat ditemukan. Mungkin halaman telah dipindahkan atau URL yang dimasukkan salah."
      bgColor="bg-orange-50"
      iconColor="text-orange-400"
    />
  );
};

export default NotFoundPage;