import React from 'react';
import BaseErrorPage from '../../components/errors/BaseErrorPage';
import { LockClosedIcon, HomeIcon } from '../../components/errors/Icons';

const ForbiddenPage = () => {
  console.log(' ForbiddenPage (403) rendered');

  const handleLogin = () => {
    console.log(' Redirecting to login...');
    localStorage.removeItem('token');
    window.location.href = '/login';
  };

  return (
    <BaseErrorPage
      icon={LockClosedIcon}
      statusCode="403"
      title="Akses Ditolak"
      message="Anda tidak memiliki izin untuk mengakses halaman ini. Silakan login dengan akun yang memiliki hak akses yang sesuai."
      bgColor="bg-red-50"
      iconColor="text-red-400"
      customActions={
        <div className="space-y-3">
          <button
            onClick={handleLogin}
            className="w-full flex items-center justify-center px-4 py-2 bg-blue-600 text-white rounded-md shadow-sm text-sm font-medium hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition duration-150"
          >
            <LockClosedIcon className="w-4 h-4 mr-2" />
            Login Ulang
          </button>
          <button
            onClick={() => window.location.href = '/'}
            className="w-full flex items-center justify-center px-4 py-2 border border-gray-300 rounded-md shadow-sm bg-white text-sm font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition duration-150"
          >
            <HomeIcon className="w-4 h-4 mr-2" />
            Ke Beranda
          </button>
        </div>
      }
    />
  );
};

export default ForbiddenPage;