import React from 'react';
import BaseErrorPage from '../../components/errors/BaseErrorPage';
import { ExclamationTriangleIcon } from '../../components/errors/Icons';

const GenericErrorPage = ({ 
  errorCode = "ERROR", 
  errorMessage = "Terjadi kesalahan yang tidak terduga" 
}) => {
  console.log(' GenericErrorPage rendered:', { errorCode, errorMessage });

  return (
    <BaseErrorPage
      icon={ExclamationTriangleIcon}
      statusCode={errorCode}
      title="Terjadi Kesalahan"
      message={errorMessage + ". Silakan coba lagi atau hubungi administrator sistem."}
      bgColor="bg-gray-50"
      iconColor="text-gray-400"
    />
  );
};

export default GenericErrorPage;