import React from 'react';
import { ArrowLeftIcon, HomeIcon } from './Icons';
import BPSLogo from './BPSLogo';

const BaseErrorPage = ({ 
  icon: Icon, 
  title, 
  message, 
  statusCode, 
  showBackButton = true, 
  showHomeButton = true,
  customActions = null,
  bgColor = "bg-red-50",
  iconColor = "text-red-400"
}) => {
  console.log(' BaseErrorPage rendered:', { statusCode, title });

  const handleGoBack = () => {
    console.log(' Going back...');
    if (window.history.length > 1) {
      window.history.back();
    } else {
      window.location.href = '/';
    }
  };

  const handleGoHome = () => {
    console.log(' Going home...');
    window.location.href = '/';
  };

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col justify-center items-center px-4">
      <div className="max-w-md w-full">
        {/* Error Icon & Status */}
        <div className={`${bgColor} rounded-lg p-8 text-center mb-6`}>
          <Icon className={`mx-auto h-16 w-16 ${iconColor} mb-4`} />
          <div className="text-6xl font-bold text-gray-400 mb-2">{statusCode}</div>
          <h1 className="text-2xl font-bold text-gray-900 mb-2">{title}</h1>
          <p className="text-gray-600 text-sm leading-relaxed">{message}</p>
        </div>

        {/* BPS Branding */}
        <BPSLogo />

        {/* Action Buttons */}
        <div className="space-y-3">
          {customActions || (
            <>
              {showBackButton && (
                <button
                  onClick={handleGoBack}
                  className="w-full flex items-center justify-center px-4 py-2 border border-gray-300 rounded-md shadow-sm bg-white text-sm font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition duration-150"
                >
                  <ArrowLeftIcon className="w-4 h-4 mr-2" />
                  Kembali
                </button>
              )}
              
              {showHomeButton && (
                <button
                  onClick={handleGoHome}
                  className="w-full flex items-center justify-center px-4 py-2 bg-blue-600 text-white rounded-md shadow-sm text-sm font-medium hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition duration-150"
                >
                  <HomeIcon className="w-4 h-4 mr-2" />
                  Ke Beranda
                </button>
              )}
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default BaseErrorPage;