import React from 'react';
import BaseErrorPage from '../../components/errors/BaseErrorPage';
import { ServerIcon, HomeIcon } from '../../components/errors/Icons';

const InternalServerErrorPage = () => {
  console.log(' InternalServerErrorPage (500) rendered');

  const handleRefresh = () => {
    console.log(' Refreshing page...');
    window.location.reload();
  };

  return (
    <BaseErrorPage
      icon={ServerIcon}
      statusCode="500"
      title="Kesalahan Server"
      message="Terjadi kesalahan pada server. Tim teknis kami sedang memperbaiki masalah ini. Silakan coba beberapa saat lagi atau hubungi administrator."
      bgColor="bg-red-50"
      iconColor="text-red-500"
      customActions={
        <div className="space-y-3">
          <button
            onClick={handleRefresh}
            className="w-full flex items-center justify-center px-4 py-2 bg-blue-600 text-white rounded-md shadow-sm text-sm font-medium hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition duration-150"
          >
            <ServerIcon className="w-4 h-4 mr-2" />
            Muat Ulang
          </button>
          <button
            onClick={() => window.location.href = '/'}
            className="w-full flex items-center justify-center px-4 py-2 border border-gray-300 rounded-md shadow-sm bg-white text-sm font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition duration-150"
          >
            <HomeIcon className="w-4 h-4 mr-2" />
            Ke Beranda
          </button>
        </div>
      }
    />
  );
};

export default InternalServerErrorPage;