// Redirect to error page utility
export const redirectToError = (errorType, errorCode = null, errorMessage = null) => {
  console.log(' Redirecting to error page:', { errorType, errorCode, errorMessage });
  
  let url = `/error/${errorType}`;
  
  if (errorCode || errorMessage) {
    const params = new URLSearchParams();
    if (errorCode) params.append('code', errorCode);
    if (errorMessage) params.append('message', errorMessage);
    url += `?${params.toString()}`;
  }
  
  console.log(' Final URL:', url);
  window.location.href = url;
};

// Handle API errors
export const handleApiError = (error) => {
  console.error(' API Error:', error);
  
  if (error.response) {
    const status = error.response.status;
    const message = error.response.data?.message || error.response.data?.error;
    
    console.log(' Error details:', { status, message });
    
    switch (status) {
      case 401:
        console.log(' Handling 401: Removing token and redirecting to unauthorized');
        localStorage.removeItem('token');
        redirectToError('unauthorized');
        break;
      case 403:
        console.log(' Handling 403: Redirecting to forbidden');
        redirectToError('forbidden');
        break;
      case 404:
        console.log(' Handling 404: Redirecting to not-found');
        redirectToError('not-found');
        break;
      case 500:
        console.log(' Handling 500: Redirecting to server-error');
        redirectToError('server-error');
        break;
      default:
        console.log(` Handling ${status}: Redirecting to generic error`);
        redirectToError('generic', status, message);
    }
  } else if (error.code === 'ERR_NETWORK') {
    console.log(' Handling network error');
    redirectToError('network');
  } else {
    console.log(' Handling unknown error');
    redirectToError('generic', null, error.message);
  }
};

// Log error utility
export const logError = (error, context = {}) => {
  const errorLog = {
    message: error.message,
    stack: error.stack,
    timestamp: new Date().toISOString(),
    url: window.location.href,
    userAgent: navigator.userAgent,
    context
  };
  
  console.error(' Error Log:', errorLog);
  
  // Di production, bisa kirim ke monitoring service
  if (process.env.NODE_ENV === 'production') {
    // sendToMonitoring(errorLog);
  }
  
  return errorLog;
};

// Test functions untuk ErrorTestPage
export const testApiErrorSimulation = (status) => {
  console.log(' Testing API error simulation:', status);
  
  const mockError = {
    response: {
      status: status,
      data: { 
        message: `Simulated ${status} error`,
        timestamp: new Date().toISOString()
      }
    }
  };
  
  // Langsung panggil handleApiError
  handleApiError(mockError);
};

export const testNetworkErrorSimulation = () => {
  console.log(' Testing network error simulation');
  
  const mockError = {
    code: 'ERR_NETWORK',
    message: 'Simulated network error - server unreachable'
  };
  
  handleApiError(mockError);
};

// Global error handler setup
export const setupGlobalErrorHandling = () => {
  // Handle unhandled promise rejections
  window.addEventListener('unhandledrejection', (event) => {
    console.error(' Unhandled promise rejection:', event.reason);
    logError(event.reason, { type: 'unhandledrejection' });
  });

  // Handle global errors
  window.addEventListener('error', (event) => {
    console.error(' Global error:', event.error);
    logError(event.error, { 
      type: 'globaleror',
      filename: event.filename,
      lineno: event.lineno,
      colno: event.colno
    });
  });

  console.log(' Global error handling setup complete');
};

// Make functions available globally for console testing
if (typeof window !== 'undefined') {
  window.testApiError = testApiErrorSimulation;
  window.testNetworkError = testNetworkErrorSimulation;
  window.redirectToError = redirectToError;
  window.logError = logError;
  
  console.log(' Global testing functions available:');
  console.log('- window.testApiError(401)');
  console.log('- window.testNetworkError()');
  console.log('- window.redirectToError("404")');
}