import React from 'react';
import BaseErrorPage from '../../components/errors/BaseErrorPage';
import { WifiIcon } from '../../components/errors/Icons';

const NetworkErrorPage = () => {
  console.log(' NetworkErrorPage rendered');

  const handleRetry = () => {
    console.log(' Retrying connection...');
    window.location.reload();
  };

  return (
    <BaseErrorPage
      icon={WifiIcon}
      statusCode="ERR"
      title="Koneksi Terputus"
      message="Tidak dapat terhubung ke server. Periksa koneksi internet Anda dan pastikan server sistem penilaian BPS sedang berjalan."
      bgColor="bg-gray-50"
      iconColor="text-gray-400"
      customActions={
        <div className="space-y-3">
          <button
            onClick={handleRetry}
            className="w-full flex items-center justify-center px-4 py-2 bg-blue-600 text-white rounded-md shadow-sm text-sm font-medium hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition duration-150"
          >
            <WifiIcon className="w-4 h-4 mr-2" />
            Coba Lagi
          </button>
          <button
            onClick={() => window.location.reload()}
            className="w-full flex items-center justify-center px-4 py-2 border border-gray-300 rounded-md shadow-sm bg-white text-sm font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition duration-150"
          >
            Muat Ulang Halaman
          </button>
        </div>
      }
    />
  );
};

export default NetworkErrorPage;