import React, { useState } from 'react';
import ErrorBoundary from '../../components/errors/ErrorBoundary';
import BPSLogo from '../../components/errors/BPSLogo';

const ErrorTestPage = () => {
  const [triggerCrash, setTriggerCrash] = useState(false);

  console.log(' ErrorTestPage rendered');

  // Component yang akan crash untuk test ErrorBoundary
  const CrashComponent = () => {
    if (triggerCrash) {
      throw new Error('Test error for ErrorBoundary!');
    }
    return <div>Component berjalan normal</div>;
  };

  const testErrorPages = [
    { 
      type: '404', 
      url: '/error/404', 
      name: '404 - Not Found',
      description: 'Halaman tidak ditemukan' 
    },
    { 
      type: '403', 
      url: '/error/403', 
      name: '403 - Forbidden',
      description: 'Akses ditolak' 
    },
    { 
      type: '401', 
      url: '/error/401', 
      name: '401 - Unauthorized',
      description: 'Sesi berakhir' 
    },
    { 
      type: '500', 
      url: '/error/500', 
      name: '500 - Server Error',
      description: 'Error server internal' 
    },
    { 
      type: 'network', 
      url: '/error/network', 
      name: 'Network Error',
      description: 'Koneksi bermasalah' 
    },
    { 
      type: 'generic', 
      url: '/error/generic?code=ERR001&message=Contoh error custom', 
      name: 'Generic Error',
      description: 'Error umum dengan parameter' 
    }
  ];

  const navigateToError = (url) => {
    console.log(' Navigating to:', url);
    window.location.href = url;
  };

  const testApiError = (status) => {
    console.log(' Testing API error:', status);
    // Simulasi API error
    const mockError = {
      response: {
        status: status,
        data: { message: `Test error ${status}` }
      }
    };
    
    import('../../utils/errorHandler').then(({ handleApiError }) => {
      handleApiError(mockError);
    });
  };

  const testNetworkError = () => {
    console.log(' Testing network error');
    const mockError = {
      code: 'ERR_NETWORK',
      message: 'Network Error'
    };
    
    import('../../utils/errorHandler').then(({ handleApiError }) => {
      handleApiError(mockError);
    });
  };

  return (
    <div className="min-h-screen bg-gray-50 py-12">
      <div className="max-w-4xl mx-auto px-4">
        {/* Header */}
        <div className="text-center mb-12">
          <BPSLogo size="large" variant="horizontal" />
          <h1 className="text-3xl font-bold text-gray-900 mt-4">Error Pages Testing</h1>
          <p className="text-gray-600 mt-2">Test semua halaman error sistem BPS</p>
        </div>

        {/* Manual Navigation Tests */}
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6 mb-8">
          <h2 className="text-xl font-semibold text-gray-900 mb-4"> Manual Navigation Tests</h2>
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-4">
            {testErrorPages.map((test) => (
              <div key={test.type} className="border border-gray-200 rounded-lg p-4">
                <h3 className="font-medium text-gray-900 mb-2">{test.name}</h3>
                <p className="text-sm text-gray-600 mb-3">{test.description}</p>
                <button
                  onClick={() => navigateToError(test.url)}
                  className="w-full px-3 py-2 bg-blue-600 text-white rounded text-sm font-medium hover:bg-blue-700 transition duration-150"
                >
                  Test {test.type.toUpperCase()}
                </button>
              </div>
            ))}
          </div>
        </div>

        {/* API Error Tests */}
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6 mb-8">
          <h2 className="text-xl font-semibold text-gray-900 mb-4"> API Error Handler Tests</h2>
          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-4">
            <button
              onClick={() => testApiError(401)}
              className="px-4 py-2 bg-yellow-600 text-white rounded text-sm font-medium hover:bg-yellow-700"
            >
              Test 401 Error
            </button>
            <button
              onClick={() => testApiError(403)}
              className="px-4 py-2 bg-red-600 text-white rounded text-sm font-medium hover:bg-red-700"
            >
              Test 403 Error
            </button>
            <button
              onClick={() => testApiError(500)}
              className="px-4 py-2 bg-purple-600 text-white rounded text-sm font-medium hover:bg-purple-700"
            >
              Test 500 Error
            </button>
            <button
              onClick={testNetworkError}
              className="px-4 py-2 bg-gray-600 text-white rounded text-sm font-medium hover:bg-gray-700"
            >
              Test Network Error
            </button>
          </div>
        </div>

        {/* Error Boundary Test */}
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6 mb-8">
          <h2 className="text-xl font-semibold text-gray-900 mb-4"> Error Boundary Test</h2>
          <p className="text-gray-600 mb-4">
            Test ErrorBoundary component yang menangkap JavaScript errors
          </p>
          
          <ErrorBoundary>
            <div className="p-4 bg-gray-50 rounded-lg mb-4">
              <CrashComponent />
            </div>
          </ErrorBoundary>
          
          <button
            onClick={() => setTriggerCrash(true)}
            className="px-4 py-2 bg-red-600 text-white rounded text-sm font-medium hover:bg-red-700"
          >
             Trigger JavaScript Error
          </button>
          
          {triggerCrash && (
            <button
              onClick={() => {
                setTriggerCrash(false);
                window.location.reload();
              }}
              className="ml-2 px-4 py-2 bg-green-600 text-white rounded text-sm font-medium hover:bg-green-700"
            >
               Reset Component
            </button>
          )}
        </div>

        {/* Logo Test */}
        <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6">
          <h2 className="text-xl font-semibold text-gray-900 mb-4"> Logo Component Tests</h2>
          <div className="space-y-6">
            <div>
              <h3 className="font-medium text-gray-700 mb-2">Horizontal Layout (Default)</h3>
              <BPSLogo variant="horizontal" />
            </div>
            
            <div>
              <h3 className="font-medium text-gray-700 mb-2">Vertical Layout</h3>
              <BPSLogo variant="vertical" />
            </div>
            
            <div>
              <h3 className="font-medium text-gray-700 mb-2">Logo Only</h3>
              <BPSLogo variant="logo-only" />
            </div>
            
            <div>
              <h3 className="font-medium text-gray-700 mb-2">Different Sizes</h3>
              <div className="flex items-center space-x-4">
                <BPSLogo size="small" variant="logo-only" />
                <BPSLogo size="medium" variant="logo-only" />
                <BPSLogo size="large" variant="logo-only" />
                <BPSLogo size="xlarge" variant="logo-only" />
              </div>
            </div>
          </div>
        </div>

        {/* Instructions */}
        <div className="mt-8 bg-blue-50 rounded-lg p-6">
          <h3 className="font-medium text-blue-900 mb-2"> Testing Instructions</h3>
          <ol className="text-sm text-blue-800 space-y-1 list-decimal list-inside">
            <li>Klik tombol "Test" untuk melihat halaman error langsung</li>
            <li>Test API Error Handler untuk simulasi error dari backend</li>
            <li>Test Error Boundary untuk melihat handling JavaScript crash</li>
            <li>Periksa console browser untuk melihat logs error</li>
            <li>Test logo component untuk memastikan fallback berfungsi</li>
            <li>Test di mobile dan desktop untuk responsiveness</li>
          </ol>
        </div>
      </div>
    </div>
  );
};

export default ErrorTestPage;