import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { AuthProvider, useAuth } from './context/AuthContext';

// Main Pages - Existing
import LoginPage from './pages/LoginPage';
import DashboardPage from './pages/DashboardPage';
import EvaluationPage from './pages/EvaluationPage';
import EvaluationManagementPage from './pages/EvaluationManagementPage';
import UsersPage from './pages/UsersPage';
import ProfilePage from './pages/ProfilePage';
import EvaluationHistoryPage from './pages/EvaluationHistoryPage';

// Additional Pages - Existing
import PeriodManagementPage from './pages/PeriodManagementPage';
import AttendanceInputPage from './pages/AttendanceInputPage';
import CkpInputPage from './pages/CkpInputPage';
import EnhancedMonitoringPage from './pages/EnhancedMonitoringPage';
import FinalCalculationPage from './pages/FinalCalculationPage';
import ComprehensiveReportPage from './pages/ComprehensiveReportPage';

// Certificate Pages - Existing
import CertificateManagementPage from './pages/CertificateManagementPage';
import CertificatePage from './pages/CertificatePage';

// Error Pages - NEW
import NotFoundPage from './pages/errors/NotFoundPage';
import ForbiddenPage from './pages/errors/ForbiddenPage';
import UnauthorizedPage from './pages/errors/UnauthorizedPage';
import InternalServerErrorPage from './pages/errors/InternalServerErrorPage';
import NetworkErrorPage from './pages/errors/NetworkErrorPage';
import GenericErrorPage from './pages/errors/GenericErrorPage';
import ErrorTestPage from './pages/errors/ErrorTestPage';

// Components - Existing + Error
import Layout from './components/Layout';
import ErrorBoundary from './components/errors/ErrorBoundary';

// Utils - NEW
import { setupGlobalErrorHandling, handleApiError } from './utils/errorHandler';

//  Enhanced ProtectedRoute with Error Handling
const ProtectedRoute = ({ children, requiredRole = null }) => {
  const { user, loading } = useAuth();
  
  console.log(' ProtectedRoute check:', { 
    user: user?.nama, 
    role: user?.role, 
    requiredRole, 
    loading 
  });
  
  if (loading) {
    return (
      <div className="d-flex justify-content-center align-items-center" style={{ height: '100vh' }}>
        <div className="spinner-border text-primary" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
      </div>
    );
  }
  
  if (!user) {
    console.log(' No user found, redirecting to login');
    return <Navigate to="/login" replace />;
  }
  
  if (requiredRole && !requiredRole.includes(user.role)) {
    console.log(' Insufficient role, redirecting to forbidden');
    return <Navigate to="/error/403" replace />;
  }
  
  console.log(' Access granted');
  return children;
};

//  Error Routes Component
const ErrorRoutes = () => {
  console.log(' ErrorRoutes rendered');

  const getErrorProps = () => {
    const params = new URLSearchParams(window.location.search);
    return {
      errorCode: params.get('code'),
      errorMessage: params.get('message')
    };
  };

  return (
    <Routes>
      {/* Standard HTTP error codes */}
      <Route path="/404" element={<NotFoundPage />} />
      <Route path="/403" element={<ForbiddenPage />} />
      <Route path="/401" element={<UnauthorizedPage />} />
      <Route path="/500" element={<InternalServerErrorPage />} />
      
      {/* Named error types */}
      <Route path="/not-found" element={<NotFoundPage />} />
      <Route path="/forbidden" element={<ForbiddenPage />} />
      <Route path="/unauthorized" element={<UnauthorizedPage />} />
      <Route path="/server-error" element={<InternalServerErrorPage />} />
      <Route path="/network" element={<NetworkErrorPage />} />
      <Route path="/generic" element={<GenericErrorPage {...getErrorProps()} />} />
      
      {/* Testing route */}
      <Route path="/test" element={<ErrorTestPage />} />
      
      {/* Fallback */}
      <Route path="/*" element={<GenericErrorPage />} />
    </Routes>
  );
};

//  Enhanced 404 Component with Layout
const Custom404Page = () => (
  <Layout>
    <div className="container-fluid p-4">
      <div className="text-center">
        <div className="row justify-content-center">
          <div className="col-md-6">
            <div className="card shadow-sm">
              <div className="card-body p-5">
                <div className="mb-4">
                  <i className="fas fa-search text-muted" style={{ fontSize: '4rem' }}></i>
                </div>
                <h1 className="display-1 text-muted">404</h1>
                <h2 className="h4 mb-3">Halaman Tidak Ditemukan</h2>
                <p className="text-muted mb-4">
                  Halaman yang Anda cari tidak tersedia di sistem BPS Assessment.
                </p>
                <div className="d-grid gap-2 d-sm-flex justify-content-sm-center">
                  <a href="/dashboard" className="btn btn-primary">
                    <i className="fas fa-home me-2"></i>
                    Kembali ke Dashboard
                  </a>
                  <a href="/error/test" className="btn btn-outline-secondary">
                    <i className="fas fa-bug me-2"></i>
                    Test Error Pages
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Layout>
);

//  Main App Component
function App() {
  React.useEffect(() => {
    console.log(' BPS Assessment System App Started');
    
    // Setup global error handling
    setupGlobalErrorHandling();
    
    // Setup API error interceptor (jika pakai axios)
    if (window.axios) {
      window.axios.interceptors.response.use(
        response => response,
        error => {
          handleApiError(error);
          return Promise.reject(error);
        }
      );
    }
    
    console.log(' Error handling setup complete');
  }, []);

  return (
    <ErrorBoundary>
      <AuthProvider>
        <Router>
          <Routes>
            {/*  PUBLIC ROUTES */}
            <Route path="/login" element={<LoginPage />} />
            
            {/*  ERROR ROUTES - Standalone (no auth required) */}
            <Route path="/error/*" element={<ErrorRoutes />} />
            
            {/*  MAIN DASHBOARD - Root redirect */}
            <Route path="/dashboard" element={
              <ProtectedRoute>
                <Layout>
                  <DashboardPage />
                </Layout>
              </ProtectedRoute>
            } />
            
            {/*  PROFILE - All authenticated users */}
            <Route path="/profile" element={
              <ProtectedRoute>
                <Layout>
                  <ProfilePage />
                </Layout>
              </ProtectedRoute>
            } />
            
            {/*  STAFF & PIMPINAN ROUTES */}
            <Route path="/evaluation" element={
              <ProtectedRoute requiredRole={['STAFF', 'PIMPINAN']}>
                <Layout>
                  <EvaluationPage />
                </Layout>
              </ProtectedRoute>
            } />
            
            <Route path="/evaluation-history" element={
              <ProtectedRoute requiredRole={['STAFF', 'PIMPINAN']}>
                <Layout>
                  <EvaluationHistoryPage />
                </Layout>
              </ProtectedRoute>
            } />

            {/*  STAFF ONLY - My Certificates */}
            <Route path="/my-certificates" element={
              <ProtectedRoute requiredRole={['STAFF']}>
                <Layout>
                  <CertificatePage />
                </Layout>
              </ProtectedRoute>
            } />
            
            {/*  ADMIN ONLY ROUTES */}
            <Route path="/evaluation-management" element={
              <ProtectedRoute requiredRole={['ADMIN']}>
                <Layout>
                  <EvaluationManagementPage />
                </Layout>
              </ProtectedRoute>
            } />
            
            <Route path="/period-management" element={
              <ProtectedRoute requiredRole={['ADMIN']}>
                <Layout>
                  <PeriodManagementPage />
                </Layout>
              </ProtectedRoute>
            } />
            
            <Route path="/attendance-input" element={
              <ProtectedRoute requiredRole={['ADMIN']}>
                <Layout>
                  <AttendanceInputPage />
                </Layout>
              </ProtectedRoute>
            } />
            
            <Route path="/ckp-input" element={
              <ProtectedRoute requiredRole={['ADMIN']}>
                <Layout>
                  <CkpInputPage />
                </Layout>
              </ProtectedRoute>
            } />
            
            <Route path="/final-calculation" element={
              <ProtectedRoute requiredRole={['ADMIN']}>
                <Layout>
                  <FinalCalculationPage />
                </Layout>
              </ProtectedRoute>
            } />

            {/*  ADMIN & PIMPINAN ROUTES */}
            <Route path="/certificate-management" element={
              <ProtectedRoute requiredRole={['ADMIN', 'PIMPINAN']}>
                <Layout>
                  <CertificateManagementPage />
                </Layout>
              </ProtectedRoute>
            } />
            
            <Route path="/users" element={
              <ProtectedRoute requiredRole={['ADMIN', 'PIMPINAN']}>
                <Layout>
                  <UsersPage />
                </Layout>
              </ProtectedRoute>
            } />
            
            <Route path="/monitoring" element={
              <ProtectedRoute requiredRole={['ADMIN', 'PIMPINAN']}>
                <Layout>
                  <EnhancedMonitoringPage />
                </Layout>
              </ProtectedRoute>
            } />

            <Route path="/reports" element={
              <ProtectedRoute requiredRole={['ADMIN', 'PIMPINAN']}>
                <Layout>
                  <ComprehensiveReportPage />
                </Layout>
              </ProtectedRoute>
            } />
            
            {/*  ROOT REDIRECT */}
            <Route path="/" element={<Navigate to="/dashboard" replace />} />
            
            {/*  404 FALLBACK - Must be last */}
            <Route path="*" element={
              <ProtectedRoute>
                <Custom404Page />
              </ProtectedRoute>
            } />
          </Routes>
        </Router>
      </AuthProvider>
    </ErrorBoundary>
  );
}

export default App;

// =============================================================================
//  frontend/src/utils/errorHandler.js - UPDATED FOR APP INTEGRATION
// =============================================================================

// Enhanced error handler dengan App.js integration
export const redirectToError = (errorType, errorCode = null, errorMessage = null) => {
  console.log(' Redirecting to error page:', { errorType, errorCode, errorMessage });
  
  let url = `/error/${errorType}`;
  
  if (errorCode || errorMessage) {
    const params = new URLSearchParams();
    if (errorCode) params.append('code', errorCode);
    if (errorMessage) params.append('message', errorMessage);
    url += `?${params.toString()}`;
  }
  
  console.log(' Final URL:', url);
  window.location.href = url;
};


// Log error utility
export const logError = (error, context = {}) => {
  const errorLog = {
    message: error.message,
    stack: error.stack,
    timestamp: new Date().toISOString(),
    url: window.location.href,
    userAgent: navigator.userAgent,
    user: JSON.parse(localStorage.getItem('user') || 'null'),
    context
  };
  
  console.error(' Error Log:', errorLog);
  
  // Send to monitoring service in production
  if (process.env.NODE_ENV === 'production') {
    // sendToMonitoring(errorLog);
  }
  
  return errorLog;
};


// Make functions available globally for testing
if (typeof window !== 'undefined') {
  window.testApiError = (status) => {
    const mockError = {
      response: { status, data: { message: `Test ${status} error` } }
    };
    handleApiError(mockError);
  };
  
  window.testNetworkError = () => {
    const mockError = { code: 'ERR_NETWORK', message: 'Test network error' };
    handleApiError(mockError);
  };
  
  window.redirectToError = redirectToError;
  window.logError = logError;
  
  console.log(' Global testing functions available');
}

