import React from 'react';
import BaseErrorPage from '../../components/errors/BaseErrorPage';
import { ShieldExclamationIcon, LockClosedIcon } from '../../components/errors/Icons';

const UnauthorizedPage = () => {
  console.log(' UnauthorizedPage (401) rendered');

  const handleLogin = () => {
    console.log(' Redirecting to login...');
    localStorage.removeItem('token');
    window.location.href = '/login';
  };

  return (
    <BaseErrorPage
      icon={ShieldExclamationIcon}
      statusCode="401"
      title="Sesi Berakhir"
      message="Sesi login Anda telah berakhir atau token tidak valid. Silakan login kembali untuk melanjutkan menggunakan sistem penilaian pegawai BPS."
      bgColor="bg-yellow-50"
      iconColor="text-yellow-400"
      showBackButton={false}
      customActions={
        <div className="space-y-3">
          <button
            onClick={handleLogin}
            className="w-full flex items-center justify-center px-4 py-2 bg-blue-600 text-white rounded-md shadow-sm text-sm font-medium hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition duration-150"
          >
            <LockClosedIcon className="w-4 h-4 mr-2" />
            Login Kembali
          </button>
        </div>
      }
    />
  );
};

export default UnauthorizedPage;